@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Roboto Mono', monospace;
  background-color: #22262d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  font-size: 16px;
  padding: 0 4rem 2rem 4rem;
}

footer {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-right: 6rem;
}

.navbar > a {
  color: white;
  padding: 0.2rem 1rem;
}

.navbar > a.active {
  color: turquoise;
}

.navbar {
  padding: 1.5rem;
  display: flex;
  align-items: center;
  max-width: 600px;
  float: right;
}

.navbar .link {
  margin-left: 2rem;
}

:root {
  --typewriterSpeed: 2s;
  --typewriterCharacters: 7;
}

.home-container {
  display: flex;
  grid-gap: 10rem;
  gap: 10rem;
  align-items: center;
  justify-content: center;
  margin: 8rem 4rem 0 4rem;
}

.home {
  width: 40vw;
}
.home h1 {
  font-size: 4.5rem;
  position: relative;
  width: -webkit-max-content;
  width: max-content;
}

/* Typewriter effect */
.home h1::before,
.home h1::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.home h1::before {
  background: #22262d;
  -webkit-animation: typewriter 2s
    steps(7) 1s forwards;
          animation: typewriter 2s
    steps(7) 1s forwards;
  -webkit-animation: typewriter var(--typewriterSpeed)
    steps(var(--typewriterCharacters)) 1s forwards;
          animation: typewriter var(--typewriterSpeed)
    steps(var(--typewriterCharacters)) 1s forwards;
}

.home h1::after {
  width: 0.125em;
  background: grey;
  -webkit-animation: typewriter 2s
      steps(7) 1s forwards,
    blink 700ms steps(7) infinite;
          animation: typewriter 2s
      steps(7) 1s forwards,
    blink 700ms steps(7) infinite;
  -webkit-animation: typewriter var(--typewriterSpeed)
      steps(var(--typewriterCharacters)) 1s forwards,
    blink 700ms steps(7) infinite;
          animation: typewriter var(--typewriterSpeed)
      steps(var(--typewriterCharacters)) 1s forwards,
    blink 700ms steps(7) infinite;
}

.home span {
  font-size: 4.5rem;
  color: orange;
}

.home button {
  font-family: Roboto Mono;
  background-color: rgb(187, 0, 187);
  color: white;
  padding: 0.5rem;
  border: white;
  border-radius: 0.2rem;
  margin: 1.2rem 0;
}

.home button:hover {
  background-color: orangered;
}

.home button:focus {
  background-color: rgb(51, 57, 68);
}

.instructions {
  width: 60vw;
}

.instructions p {
  text-align: right;
  font-size: 1.4rem;
}

@-webkit-keyframes typewriter {
  to {
    left: 100%;
  }
}

@keyframes typewriter {
  to {
    left: 100%;
  }
}

@-webkit-keyframes blink {
  to {
    background: transparent;
  }
}

@keyframes blink {
  to {
    background: transparent;
  }
}

.card-container {
  display: grid;
  grid-template-columns: repeat(5, 16.5vw);
  grid-auto-rows: auto;
  grid-gap: 2rem;
}

.userCard {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin: 1.5rem 0;
  border: none;
  background-color: rgb(45, 54, 74);
  border-radius: 0.5rem;
  font-size: 0.8rem;
  overflow-wrap: break-word;
  align-items: center;
}

.userCard:hover {
  box-shadow: 4px 3px 6px rgba(0, 0, 0, 0.447);
  background-color: turquoise;
  color: black;
}

.userCard button {
  font-family: Roboto Mono;
  padding: 0.5rem;
  color: white;
  background-color: darkslategrey;
  border: white;
  border-radius: 0.2rem;
  margin: 1.2rem 0;
}

.userCard button:hover {
  background-color: orangered;
}

.userCard img {
  max-width: 80px;
  border-radius: 50%;
}

.svgLoader {
  margin: 4rem auto;
}

.tableRow {
  background-color: rgba(65, 73, 90, 0.287);
}

.tableRow:hover {
  background-color: rgb(235, 235, 235);
}

img {
  border-radius: 40%;
}

.svgLoader {
  margin: 4rem 4rem;
}

.notFound {
  display: flex;
  flex-direction: column;
  margin: 4rem auto;
  justify-content: center;
  align-items: center;
}

.notFound h1 {
  color: orangered;
}

.filteredUser-card {
  background-color: rgb(45, 54, 74);
  padding: 3rem;
  width: 60vw;
  display: flex;
  margin: 6rem auto;
  grid-gap: 4rem;
  gap: 4rem;
}

